import React from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import { Result, WhiteSpace, List, WingBlank, Button, Modal } from 'antd-mobile'
import browserCookie from 'browser-cookies'

import { logoutSubmit } from '../../redux/user.redux'

@connect(
	state=>state.user,
	{ logoutSubmit }
)
class User extends React.Component{
	constructor(props){
		super(props)
		this.logoutCookie = this.logoutCookie.bind(this)
	}

	logoutCookie(){
		const alert = Modal.alert
		alert('','',[
			{ text: '', onPress: () => console.log('cancel') },
			{ text: '', onPress: () => {
					browserCookie.erase('userId')
					this.props.logoutSubmit()
			}}
		])
	}

	render(){
		const props = this.props

		const Item = List.Item
		const Brief = Item.Brief

		return props.avatar ? (
			<div>
				<Result
					img={<img src={require(`../img/${props.avatar}.png`)} style={{width:50}} alt=""/>}
					title={props.user}
					message={ props.company ? props.company : null }
				/>
				<WhiteSpace />
				<List
					renderHeader={()=> props.type === 'boss' ? '' : ''}
				>
					<Item
						multipleLine
						onClick={this.logoutCookie}
					>
						{props.title}
						<Brief>{props.desc.split('\n').map(v=><div key={v}>{v}</div>)}</Brief>
					</Item>
				</List>
				<WhiteSpace />
				<WhiteSpace />
				<WingBlank>
					<Button
						type="primary"
						onClick={this.logoutCookie}
					></Button>
				</WingBlank>
			</div>
		): <Redirect to={this.props.redirectTo}	/>
	}
}

export default User


