import React from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import { NavBar,List,InputItem, WingBlank, WhiteSpace, Button, TextareaItem } from 'antd-mobile'

import AvatarSelector from '../../component/avatar-selector/avatarSelector'
import {update} from '../../redux/user.redux'

@connect(
	state=>state.user,
	{update}
)
class BossInfo extends React.Component{
	constructor(props){
		super(props)
		this.state={
			title:'',
			company: '',
			money: '',
			desc: ''
		}
		this.selectAvatar = this.selectAvatar.bind(this)
	}

	handleChange(key, val){
		this.setState({
			[key]:val
		})
	}

	selectAvatar(elm){
		this.setState({
			'avatar': elm
		})
	}

	render(){
		const path = this.props.location.pathname
		const redirectTo = this.props.redirectTo
		return (
			<div>
			{redirectTo && redirectTo !== path ? <Redirect to={this.props.redirectTo} /> : null}
				<NavBar
					mode="dark"
				>BOSS</NavBar>
				<AvatarSelector
					selectAvatar={this.selectAvatar}
					img={this.state.avatar}
				>

				</AvatarSelector>
				<List>
					<InputItem
						onChange={v=>this.handleChange('title',v)}
						placeholder=''
					>
						
					</InputItem>
					<InputItem
						onChange={v=>this.handleChange('company',v)}
						placeholder=''
					>
						
					</InputItem>
					<InputItem
						onChange={v=>this.handleChange('money',v)}
						placeholder=''
					>
						
					</InputItem>
					<TextareaItem
						rows={3}
						autoHeight
						title=''
						onChange={v=>this.handleChange('desc',v)}
						placeholder=''
					>
					</TextareaItem>
				</List>
				<WhiteSpace />
				<WhiteSpace />
				<WingBlank>
					<Button
						type='primary'
						onClick={()=>{
							this.props.update(this.state)
						}}
					></Button>
				</WingBlank>
			</div>
		)
	}
}

export default BossInfo


